import React from "react";
import ReactDOM from "react-dom";
import $ from "jquery";
import logo from "./logo.svg";
import "./App.css";

class App extends React.Component {
  render() {
    return (
      <div>
        <Header />

        <Content />
      </div>
    );
  }
}

class Header extends React.Component {
  render() {
    return (
      <div>
        <h1 className="center">Витрина</h1>
      </div>
    );
  }
}

class ProductFilterMenu extends React.Component {
  constructor() {
    super();
    this.handleChange = this.handleChange.bind(this);
    this.currencyChange = this.currencyChange.bind(this);
  }
  handleChange() {
    this.props.onFormInput(
      ReactDOM.findDOMNode(this.refs["seriesInput"]).value,

      ReactDOM.findDOMNode(this.refs["genreInput"]).value,

      ReactDOM.findDOMNode(this.refs["gradeInput"]).value,
      ReactDOM.findDOMNode(this.refs["searchBlock"]).value
    );
  }
  handleClick = event => {
    if (event.keyCode === 13) {
      console.log("да");

      this.props.onFormInput(
        ReactDOM.findDOMNode(this.refs["seriesInput"]).value,

        ReactDOM.findDOMNode(this.refs["genreInput"]).value,

        ReactDOM.findDOMNode(this.refs["gradeInput"]).value,
        ReactDOM.findDOMNode(this.refs["searchBlock"]).value
      );

      event.preventDefault();
    }
  };

  currencyChange() {
    let viewOneHeader = document.getElementById("content");
    viewOneHeader.classList.toggle("bonus");
  }

  render() {
    return (
      <form className="filter-menu">
        <select id="seriesInput" ref="seriesInput" onChange={this.handleChange}>
          <option value="All">Все предметы</option>
          {this.props.subjectNames.map(function(item, key) {
            return (
              <option value={item} key={item}>
                {item}
              </option>
            );
          })}
        </select>
        <select id="genreInput" ref="genreInput" onChange={this.handleChange}>
          <option value="All">Все жанры</option>
          {this.props.genreNames.map(function(item, key) {
            return (
              <option value={item} key={item}>
                {item}
              </option>
            );
          })}
        </select>
        <select id="gradeInput" ref="gradeInput" onChange={this.handleChange}>
          <option value="All">Все классы</option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value="11">11</option>
        </select>
        <select
          className="priceSwitch"
          id="priceInput"
          ref="priceInput"
          onChange={this.currencyChange}
        >
          <option value="рубли">рубли</option>
          <option value="бонусы">бонусы</option>
        </select>
        <div>
          <input
            id="searchBlock"
            ref="searchBlock"
            className="borderFind"
            type="text"
            placeholder="Поиск"
            onKeyDown={this.handleClick}
          />
          <button
            className="courses-form-search-btn"
            type="button"
            title="Найти"
            onClick={this.handleChange}
          />
        </div>
      </form>
    );
  }
}

class ProductFilterResults extends React.Component {
  constructor() {
    super();
    this.state = {
      pfdata: []
    };
  }
  render() {
    var results = [];
    var emptyValue;

    this.props.mainData.map(product => {
      if (emptyValue !== 2) {
        emptyValue = 0;
      }

      if (this.props.subject === 0) {
        results.push(<Product product={product} key={product.courseId} />);
        emptyValue = 1;
      } else if (
        (this.props.subject === "All" ||
          product.subject === this.props.subject) &&
        (this.props.genre === "All" || product.genre === this.props.genre) &&
        (this.props.grade === "All" ||
          product.grade.indexOf(this.props.grade) !== -1) &&
        (this.props.searchInput === "" ||
          (this.props.searchInput !== "" &&
            (product.subject
              .toUpperCase()
              .indexOf(this.props.searchInput.toUpperCase()) !== -1 ||
              product.genre
                .toUpperCase()
                .indexOf(this.props.searchInput.toUpperCase()) !== -1 ||
              product.grade
                .toUpperCase()
                .indexOf(this.props.searchInput.toUpperCase()) !== -1)))
      ) {
        results.push(<Product product={product} key={product.courseId} />);
        emptyValue = 2;
      }
      return null;
    });

    if (emptyValue === 0) {
      results.push("курсы не найдены");
    }

    return <ul className="courselist">{results}</ul>;
  }
}

class Product extends React.Component {
  constructor() {
    super();
    this.makeGrade = this.makeGrade.bind(this);
  }

  makeGrade(gradestr) {
    var myArr = gradestr.split(";");
    var gradeResult;
    if (myArr.length > 1) {
      gradeResult = myArr[0] + "-" + myArr[myArr.length - 1] + " классы";
    } else {
      gradeResult = myArr + " класс";
    }
    this.resultGrade = gradeResult;
  }

  render() {
    this.makeGrade(this.props.product.grade);

    return (
      <li className="courses-sci">
        <div className="sci-figure">
          <img src={"./img/" + this.props.product.courseId + ".jpg"} alt="" />
        </div>
        <div className="sci-info">
          <p className="sci-title">{this.props.product.subject}</p>
          <p className="sci-grade hidden">{this.props.product.grade}</p>
          <p className="sci-grade">{this.resultGrade}</p>
          <p className="sci-genre">{this.props.product.genre}</p>
          <p className="sci-meta sci-price">
            <a href={this.props.product.shopUrl}>Подробнее</a>
          </p>
          <p className="sciprice price-block ">
            {this.props.product.price + " рублей"}
          </p>
          <p className="scipricebonus price-block">
            {this.props.product.priceBonus + " бонусов"}
          </p>
        </div>
      </li>
    );
  }
}

class Content extends React.Component {
  constructor(props) {
    super(props);

    this.handleFormInput = this.handleFormInput.bind(this);
    /*this.handleSearchInput = this.handleSearchInput.bind(this);*/

    this.state = {
      data: [],
      subjectNames: [],
      gradeNames: [],
      genreNames: [],
      subject: 0,
      genre: 0,
      grade: 0,
      searchInput: "",
      filter: null
    };
  }

  handleFormInput(subject, genre, grade, searchInput) {
    this.setState({
      subject: subject,
      genre: genre,
      grade: grade,
      searchInput: searchInput
    });
  }

  render() {
    return (
      <div>
        <ProductFilterMenu
          subjectNames={this.state.subjectNames}
          genreNames={this.state.genreNames}
          gradeNames={this.state.gradeNames}
          subject={this.state.subject}
          genre={this.state.genre}
          grade={this.state.grade}
          onFormInput={this.handleFormInput}
        />

        <ProductFilterResults
          mainData={this.state.data}
          subject={this.state.subject}
          genre={this.state.genre}
          grade={this.state.grade}
          searchInput={this.state.searchInput}
        />
      </div>
    );
  }
  componentDidMount() {
    $.ajax({
      url: "http://krapipl.imumk.ru:8082/api/mobilev1/update",
      type: "POST",

      ContentType: "application/json",
      crossDomain: "true",

      data: { data: "" },

      success: function(indata) {
        var data = this.state.data.concat(indata["items"]);

        function getFields(input, field) {
          var output = [];
          for (var i = 0; i < input.length; ++i) output.push(input[i][field]);
          return output;
        }
        var subject = getFields(data, "subject");
        var genre = getFields(data, "genre");
        var grade = getFields(data, "grade");
        var subjectNames = [];
        var genreNames = [];
        var gradeNames = [];

        $.each(subject, function(i, el) {
          if ($.inArray(el, subjectNames) === -1) subjectNames.push(el);
        });
        $.each(genre, function(i, el) {
          if ($.inArray(el, genreNames) === -1) genreNames.push(el);
        });
        $.each(grade, function(i, el) {
          if ($.inArray(el, gradeNames) === -1) gradeNames.push(el);
        });
        this.setState({
          data: data,
          subjectNames: subjectNames,
          genreNames: genreNames,
          gradeNames: gradeNames
        });
      }.bind(this),
      error: function(jqXHR) {}
    });
  }
}

export default App;
